@font-face {
    font-family: 'arcade';
    src: local('arcade'), url(../resources/fonts/karmatic-arcade/ka1.ttf) format('truetype');
  }


:root {
    --bgCol:rgb(14, 6, 59);
    --textCol:rgb(255, 255, 255);
    --highlightCol: rgb(64, 211, 174);
    --accentCol: rgb(204, 27, 195);
}

.navbar {
    background-color: #212529ee;
    border-bottom-color: var(--highlightCol);
    border-bottom-width: 2px;
    border-bottom-style: solid;
    display: flex;
    position: fixed;
    z-index: 998;
    height: 50px;
    width: 100%;
    font-size: 40px;
    color: aliceblue;
    align-content: space-around;
    justify-content: left;
}

.menuIcon {
    padding-top: 1px;
}

.menuIcon:hover{
    color: var(--accentCol);
}

.menu{
    background-color:#212529ee;
    width: 440px;
    height: 100vh; 
    display: grid;
    grid-template-columns: 100px 100px 100px 100px;
    grid-template-rows: 50px 50px 50px 50px;
    grid-gap: 10px;
    justify-content: left;
    /* overflow: hidden; */
    position: fixed; 
    top: 52px;
    padding-top: 20px;
    transition: 500ms;
    padding-left: 10px;
    font-size: 10px;   
    color: var(--textColor);
    z-index: 999;
}

.menu_show {    
    left: 0px;
}

.menu_hide {
    left: -450px;
}

.menuTitle {
    grid-column: 1/ span 4;
    color: var(--highlightCol);
    position: relative;
    font-family: Arial;
    font-size: 20px;
    width: auto;
    height: auto;
    padding-bottom: 20px;
}

.menuTabs {
    background-color: #00000000;
    border: 1px solid var(--textCol);
    color: var(--textCol);
    font-weight: bold;
    position: relative;
    border-radius: 3px;
    width: 100px;
    min-width: 100px;
    height: 32px;
    top: 0px;
    font-size: small;
    padding: 0px;
}

.menuTab1 {
    grid-column:"1/ span 1";
}
.menuTab1 {
    grid-column:"2/ span 1";
}
.menuTab1 {
    grid-column:"3/ span 1";
}


.inspectorContent
{
    outline: 1px dotted green;
}

.menuButtons
{
    /* display: table; */
    position: static;
    /* left:20px;
    height: 50px; */
    /* top: 200px;  */
    /* min-height: 50px; */
}

.menuTabs:hover
{
    background-color: var(--highlightCol);
    color: black;
}

.librarySubtitle {
    font-size: medium;
    color: var(--textCol);
    padding-bottom: 10px;
}

.libraryLayout {
    display: grid;
    grid-template-columns: 120px 120px 120px;
    grid-gap: 10px;
 
    color: #444;
  }

  .libraryObjectContainer {
    grid-column: 1 / 4;
  }




.inspectorTable
{
    width: 450px;
}

.inspectorTitle {
    position: relative;
    font-size: x-large;
    font-weight: bold;
    padding-bottom: 10px;
    width: 450px;
    /* border: 1px white solid; */
}

.inspectorSubTitle{
    position: relative;
    font-size: large;
    font-weight: bold;
    padding-bottom: 10px;
    width: 450px;
}

.inspectorDivider{
    text-align: center;
    font-size: xx-large;
    padding-top: 20px;
    padding-bottom: 20px;
}

.inspectorProp
{
    font-size: medium;
    font-weight: bold;
    width: 180px;
    padding-left: 20px;
    text-align: right;
    background-color: #222222;
    border-radius: 5px;
    margin: 4px;
    padding: 4px;
    border: 1px white solid;
}

.inspectorValue
{
    float:left;
    width: 250px;
    font-size: medium;
    text-align: left;
    background-color: #222222;
    border-radius: 5px;
    margin: 4px;
    padding: 4px;
    /* border: 1px white solid; */
}



.dndnode {
  height: 20px;
  width:100px;
  padding: 4px;
  color: #fff;
  border: 2px solid #666666;
  background-color: #000000;
  border-radius: 5px;
  margin-bottom: 10px;
  text-align: center;

  justify-content: center;
  align-items: center;
  cursor: grab;
}


